/***** Theme Colors *****/
/***** Brand Colors *****/
.mfp-content {
  height: 90%;
  max-height: 560px;
}

.slide {
  background: #2b2b2b;
  text-align: left;
  max-width: 960px;
  margin: 0 auto;
  position: relative;
  width: 90%;
  height: 100%;
}

.slide .img {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.slide .content {
  padding: 1rem 2rem;
  position: absolute;
  top: 60%;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(43, 43, 43, 0.65);
}

.slide p {
  color: #7c7a7a;
}

.mfp-close-btn-in .mfp-close {
  color: #ffffff;
  opacity: 1;
}

/**
 * Fade-zoom animation for first dialog
 */
/* start state */
.my-mfp-zoom-in .project-gallery {
  opacity: 0;
  transition: all 0.2s ease-in-out;
  transform: scale(0.8);
}

/* animate in */
.my-mfp-zoom-in.mfp-ready .project-gallery {
  opacity: 1;
  transform: scale(1);
}

/* animate out */
.my-mfp-zoom-in.mfp-removing .project-gallery {
  transform: scale(0.8);
  opacity: 0;
}

/* Dark overlay, start state */
.my-mfp-zoom-in.mfp-bg {
  opacity: 0;
  transition: opacity 0.3s ease-out;
}

/* animate in */
.my-mfp-zoom-in.mfp-ready.mfp-bg {
  opacity: 0.8;
}

/* animate out */
.my-mfp-zoom-in.mfp-removing.mfp-bg {
  opacity: 0;
}

* {
  box-sizing: border-box;
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

body {
  font-family: 'Lato', sans-serif;
  position: relative;
  background-color: #2b2b2b;
  color: #7c7a7a;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  margin: 0;
  color: white;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

pre, code {
  background: #e8e8e8;
  padding: 2px 4px;
  border-radius: 4px;
}

pre {
  padding: 10px;
  overflow-x: auto;
}

pre code {
  padding: 0;
  border-radius: 0;
}

a {
  text-decoration: none;
  color: #11abb0;
}

a:hover, a:focus {
  color: #f06000;
}

img {
  max-width: 100%;
}

img.profile {
  border-radius: 50%;
  max-width: 225px;
  margin: 2rem auto;
  display: block;
  border: 2px solid #ffffff;
}

main {
  padding: 83px 1rem 168px;
  max-width: 1024px;
  margin: 0 auto;
}

.wrap {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  max-width: 1024px;
  margin: 0 auto;
}

.nav {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}

.nav li {
  flex: 0 0 auto;
  padding: 1rem;
}

.social.sharing li {
  padding: 0.5rem;
}

.social.sharing a {
  margin: 0;
}

.social li {
  flex: 1;
  height: 2.5rem;
  padding: 0 0.5rem;
}

.social a {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  text-align: center;
  margin: 0 auto;
}

.social a.BitBucket {
  background-color: #55acee;
}

.social a.Email {
  background-color: #ea4335;
}

.social a.Facebook {
  background-color: #3b5998;
}

.social a.GitHub {
  background-color: #4183c4;
}

.social a.GooglePlus {
  background-color: #ea4335;
}

.social a.Instagram {
  background-color: #000;
}

.social a.Reddit {
  background-color: #5f99cf;
}

.social a.Twitter {
  background-color: #55acee;
}

.social .icon {
  color: white;
  position: relative;
  font-size: 1.25rem;
  top: 10px;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.row > .col {
  flex: 0 0 auto;
}

.row > .col.one {
  width: 8.3333333333%;
}

.row > .col.two {
  width: 16.6666666667%;
}

.row > .col.three {
  width: 25%;
}

.row > .col.four {
  width: 33.3333333333%;
}

.row > .col.five {
  width: 41.6666666667%;
}

.row > .col.six {
  width: 50%;
}

.row > .col.seven {
  width: 58.333333333%;
}

.row > .col.eight {
  width: 66.6666666667%;
}

.row > .col.nine {
  width: 75%;
}

.row > .col.ten {
  width: 83.3333333333%;
}

.row > .col.eleven {
  width: 91.6666666667%;
}

.row > .col.twelve {
  width: 100%;
}

.list .row:not(:last-child) {
  margin-bottom: 2rem;
}

.headroom {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 1rem 0;
  z-index: 10;
  background-color: #2b2b2b;
  transition: transform 200ms linear;
}

.headroom.headroom--unpinned {
  transform: translateY(-100%);
}

.headroom.headroom--pinned {
  transform: translateY(0);
}

.headroom .wrap {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  max-width: 1056px;
  margin: 0 auto;
}

.headroom .logo {
  flex: 0 0 auto;
  padding: 1rem;
}

.headroom a {
  color: #ffffff;
}

.headroom a:hover, .headroom a:focus {
  color: #f06000;
}

#footer {
  padding: 1rem;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  max-width: 100%;
  background-color: black;
  justify-content: center;
}

#footer .scroll {
  position: absolute;
  left: 50%;
  top: -30px;
  transform: translateX(-50%);
  background-color: #7c7a7a;
  border-radius: 50%;
  height: 60px;
  width: 60px;
  font-size: 1.25rem;
  text-align: center;
  color: white;
}

#footer .scroll:hover, #footer .scroll:focus {
  background-color: #11abb0;
}

#footer .scroll .icon {
  position: relative;
  top: 45%;
  transform: translateY(-45%);
}

#footer .social {
  flex: 0 0 auto;
  width: 100%;
  justify-content: center;
  margin: 2rem 0 1rem;
}

#footer .social li {
  flex: 0 0 auto;
}

#footer .social a {
  background-color: inherit;
}

#footer .social .icon {
  color: #7c7a7a;
}

.copyright {
  flex: 0 0 auto;
  width: 100%;
  margin: 0;
  text-align: center;
  color: #2b2b2b;
}

button, .button {
  display: block;
  max-width: 300px;
  margin: 2rem auto;
  border: 2px solid #11abb0;
  border-radius: 4px;
  background-color: #444444;
  color: #ffffff;
  padding: 1rem 2rem;
  text-align: center;
  transition: all 0.5s ease;
}

button:hover, button:focus, .button:hover, .button:focus {
  background-color: #11abb0;
  color: #ffffff;
}

button.read-more, .button.read-more {
  margin: 1rem 0 2rem;
  padding: 0.5rem;
  max-width: 120px;
}

button.paginate, .button.paginate {
  flex: 1;
  padding: 0.5rem 0.25rem;
  background-color: #7c7a7a;
}

button.paginate[disabled], .button.paginate[disabled] {
  opacity: 0.25;
}

button.paginate[disabled]:hover, button.paginate[disabled]:focus, .button.paginate[disabled]:hover, .button.paginate[disabled]:focus {
  background-color: #7c7a7a;
  color: #ffffff;
}

button.paginate[disabled].current, .button.paginate[disabled].current {
  opacity: 1;
  background-color: #f06000;
  border-color: #8a3700;
  color: #cccccc;
}

button.paginate[disabled].current:hover, button.paginate[disabled].current:focus, .button.paginate[disabled].current:hover, .button.paginate[disabled].current:focus {
  background-color: #f06000;
}

button.paginate:hover, button.paginate:focus, .button.paginate:hover, .button.paginate:focus {
  background-color: #11abb0;
}

.home > div {
  min-height: calc(100vh - 85px);
  position: relative;
}

.home > div h2 {
  margin: 2rem auto;
  border-bottom: 1px solid #11abb0;
  padding-bottom: 0.5rem;
  font-size: 2rem;
}

#landing .content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
}

#landing .scroll {
  position: absolute;
  bottom: 1rem;
  left: 50%;
  transform: translateX(-50%);
}

#landing .scroll a {
  display: block;
  font-size: 2.5rem;
}

#about h3 {
  margin-bottom: 0.5rem;
}

#about .icon {
  margin-right: 0.5rem;
  font-size: 1.25rem;
}

#about li:not(:last-child) {
  margin-bottom: 0.5rem;
}

@media (max-width: 766px) {
  #about .col {
    width: 100%;
  }
}

@media (min-width: 767px) {
  #about {
    position: relative;
  }
  #about > .row {
